.form {
  flex-direction: column;
  display: flex;
}

label {
  color: #305253;
  flex-direction: column;
  display: flex;
}

input {
  max-width: 250px;
  border: 1px solid #477474;
  margin: 5px 0;
  padding: 5px;
}

input:focus {
  border: 1px solid #4d4d83;
  outline: 1px solid #4d4d83;
}

button {
  max-width: 250px;
  color: #484851;
  text-transform: uppercase;
  background-color: #1ab0b0;
  border: none;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 600;
}

button:hover {
  background-color: #93e5e5;
}

button:active {
  background-color: #74b8b8;
}

